import { AnimationType, AnimationConfig, CustomAnimation } from './types';

export const animationPresets: Record<AnimationType, AnimationConfig> = {
  // Fade animations
  fade: {
    initial: { opacity: 0 },
    animate: { opacity: 1 },
  },
  fadeUp: {
    initial: { opacity: 0, y: 30 },
    animate: { opacity: 1, y: 0 },
  },
  fadeDown: {
    initial: { opacity: 0, y: -30 },
    animate: { opacity: 1, y: 0 },
  },
  fadeLeft: {
    initial: { opacity: 0, x: 30 },
    animate: { opacity: 1, x: 0 },
  },
  fadeRight: {
    initial: { opacity: 0, x: -30 },
    animate: { opacity: 1, x: 0 },
  },

  // Slide animations
  slideUp: {
    initial: { y: 100, opacity: 0 },
    animate: { y: 0, opacity: 1 },
  },
  slideDown: {
    initial: { y: -100, opacity: 0 },
    animate: { y: 0, opacity: 1 },
  },
  slideLeft: {
    initial: { x: 100, opacity: 0 },
    animate: { x: 0, opacity: 1 },
  },
  slideRight: {
    initial: { x: -100, opacity: 0 },
    animate: { x: 0, opacity: 1 },
  },

  // Zoom animations
  zoom: {
    initial: { scale: 0.8, opacity: 0 },
    animate: { scale: 1, opacity: 1 },
  },
  zoomIn: {
    initial: { scale: 0, opacity: 0 },
    animate: { scale: 1, opacity: 1 },
  },
  zoomOut: {
    initial: { scale: 1.2, opacity: 0 },
    animate: { scale: 1, opacity: 1 },
  },

  // Rotate animations
  rotate: {
    initial: { rotate: 180, opacity: 0 },
    animate: { rotate: 0, opacity: 1 },
  },
  rotateLeft: {
    initial: { rotate: -90, opacity: 0, scale: 0.9 },
    animate: { rotate: 0, opacity: 1, scale: 1 },
  },
  rotateRight: {
    initial: { rotate: 90, opacity: 0, scale: 0.9 },
    animate: { rotate: 0, opacity: 1, scale: 1 },
  },

  // Flip animations
  flip: {
    initial: { rotateY: 90, opacity: 0 },
    animate: { rotateY: 0, opacity: 1 },
  },
  flipX: {
    initial: { rotateX: 90, opacity: 0 },
    animate: { rotateX: 0, opacity: 1 },
  },
  flipY: {
    initial: { rotateY: 90, opacity: 0 },
    animate: { rotateY: 0, opacity: 1 },
  },

  // Special animations
  blur: {
    initial: { filter: 'blur(10px)', opacity: 0 },
    animate: { filter: 'blur(0px)', opacity: 1 },
  },
  bounce: {
    initial: { y: -30, opacity: 0 },
    animate: {
      y: 0,
      opacity: 1,
      transition: {
        type: 'spring',
        stiffness: 300,
        damping: 20,
      },
    },
  },
};

/**
 * Create a custom animation configuration
 *
 * @example
 * ```tsx
 * const customFade = createAnimation({
 *   initial: { opacity: 0, scale: 0.5 },
 *   animate: { opacity: 1, scale: 1 }
 * });
 * ```
 */
export function createAnimation(config: AnimationConfig): CustomAnimation {
  return {
    ...config,
    name: 'custom',
    description: 'Custom animation configuration',
  };
}

/**
 * Combine multiple animations
 *
 * @example
 * ```tsx
 * const combined = combineAnimations(
 *   animationPresets.fade,
 *   animationPresets.slideUp
 * );
 * ```
 */
export function combineAnimations(...animations: AnimationConfig[]): AnimationConfig {
  return animations.reduce(
    (acc, anim) => ({
      initial: { ...(acc.initial as any), ...(anim.initial as any) },
      animate: { ...(acc.animate as any), ...(anim.animate as any) },
    }),
    { initial: {}, animate: {} }
  );
}
