import { gsap } from 'gsap';
import { isSSR } from '../utils/ssr';
import { Transition } from '@headlessui/react';
import { motion, Variants } from 'framer-motion';
import { useScrollReveal } from '../hooks/useScrollReveal';
import { ScrollRevealProps } from '../animations/types';
import { animationPresets } from '../animations/presets';
import { useScrollRevealContext } from '../context/ScrollRevealProvider';
import React, { useRef, useEffect, ReactElement, CSSProperties } from 'react';

/**
 * ScrollReveal Component
 *
 * A type-safe, production-ready scroll animation component that reveals
 * elements as they enter the viewport using IntersectionObserver.
 *
 * @example
 * ```tsx
 * <ScrollReveal animationType="fade" delay={200}>
 *   <div>Content to reveal</div>
 * </ScrollReveal>
 * ```
 */
export const ScrollReveal: React.FC<ScrollRevealProps> = ({
  children,
  animationType = 'fade',
  customAnimation,
  delay = 0,
  duration = 600,
  once = true,
  threshold = 0.1,
  rootMargin = '0px',
  disabled = false,
  engine = 'framer',
  className,
  style,
  onReveal,
  onHide,
  ...rest
}) => {
  const elementRef = useRef<HTMLDivElement>(null);
  const animationRef = useRef<gsap.core.Tween | null>(null);
  const context = useScrollRevealContext();

  // Merge with global config if available
  const config = {
    animationType: animationType ?? context?.animationType ?? 'fade',
    delay: delay ?? context?.delay ?? 0,
    duration: duration ?? context?.duration ?? 600,
    once: once ?? context?.once ?? true,
    threshold: threshold ?? context?.threshold ?? 0.1,
    rootMargin: rootMargin ?? context?.rootMargin ?? '0px',
    engine: engine ?? context?.engine ?? 'framer',
  };

  const { isInView, hasAnimated } = useScrollReveal({
    ref: elementRef,
    threshold: config.threshold,
    rootMargin: config.rootMargin,
    once: config.once,
    disabled: disabled || isSSR(),
  });

  // Get animation configuration
  const animation = customAnimation || animationPresets[config.animationType];

  if (!animation) {
    throw new Error(`Invalid animation type: ${config.animationType}`);
  }

  // Handle GSAP animations
  useEffect(() => {
    if (config.engine !== 'gsap' || !elementRef.current || isSSR()) return;

    const element = elementRef.current;

    if (isInView && !hasAnimated) {
      // Kill any existing animation
      if (animationRef.current) {
        animationRef.current.kill();
      }

      // Apply initial state
      gsap.set(element, animation.initial as any);

      // Animate to final state
      animationRef.current = gsap.to(element, {
        ...(animation.animate as any),
        duration: config.duration / 1000,
        delay: config.delay / 1000,
        ease: 'power2.out',
        onComplete: () => {
          onReveal?.();
        },
      });
    } else if (!isInView && !config.once) {
      // Reset animation
      if (animationRef.current) {
        animationRef.current.kill();
      }
      gsap.set(element, animation.initial as any);
      onHide?.();
    }

    return () => {
      if (animationRef.current) {
        animationRef.current.kill();
      }
    };
  }, [isInView, hasAnimated, animation, config, onReveal, onHide]);

  // Validate children
  const child = React.Children.only(children) as ReactElement;

  // Framer Motion implementation
  if (config.engine === 'framer') {
    const variants: Variants = {
      hidden: animation.initial as any,
      visible: {
        ...(animation.animate as any),
        transition: {
          duration: config.duration / 1000,
          delay: config.delay / 1000,
          ease: 'easeOut',
        },
      },
    };

    return (
      <motion.div
        ref={elementRef}
        initial="hidden"
        animate={isInView ? 'visible' : 'hidden'}
        variants={variants}
        className={className}
        style={style}
        onAnimationComplete={(definition) => {
          if (definition === 'visible') {
            onReveal?.();
          } else {
            onHide?.();
          }
        }}
        {...rest}
      >
        {child}
      </motion.div>
    );
  }

  // Headless UI Transition implementation
  if (config.engine === 'headless') {
    const transitionStyles: Record<string, CSSProperties> = {
      enter: {
        transition: `all ${config.duration}ms ease-out ${config.delay}ms`,
      },
      enterFrom: animation.initial as CSSProperties,
      enterTo: animation.animate as CSSProperties,
      leave: {
        transition: `all ${config.duration}ms ease-in`,
      },
      leaveFrom: animation.animate as CSSProperties,
      leaveTo: animation.initial as CSSProperties,
    };

    return (
      <div ref={elementRef} className={className} style={style} {...rest}>
        <Transition
          show={isInView}
          appear={true}
          {...Object.entries(transitionStyles).reduce(
            (acc, [key, value]) => ({
              ...acc,
              [key]: Object.entries(value)
                .map(([prop, val]) => `${prop}: ${val}`)
                .join('; '),
            }),
            {}
          )}
          afterEnter={onReveal}
          afterLeave={onHide}
        >
          {child}
        </Transition>
      </div>
    );
  }

  // GSAP implementation (render wrapper only)
  return (
    <div ref={elementRef} className={className} style={style} {...rest}>
      {child}
    </div>
  );
};

ScrollReveal.displayName = 'ScrollReveal';
