import React, { createContext, useContext, ReactNode } from 'react';
import { AnimationType, AnimationEngine } from '../animations/types';

export interface ScrollRevealConfig {
  /** Default animation type */
  animationType?: AnimationType;
  /** Default animation delay */
  delay?: number;
  /** Default animation duration */
  duration?: number;
  /** Default once behavior */
  once?: boolean;
  /** Default threshold */
  threshold?: number | number[];
  /** Default root margin */
  rootMargin?: string;
  /** Default animation engine */
  engine?: AnimationEngine;
  /** Global disable flag */
  disabled?: boolean;
  /** Enable reduced motion support */
  respectReducedMotion?: boolean;
}

export interface ScrollRevealProviderProps {
  children: ReactNode;
  config?: ScrollRevealConfig;
}

const ScrollRevealContext = createContext<ScrollRevealConfig | null>(null);

/**
 * Global configuration provider for ScrollReveal components
 *
 * @example
 * ```tsx
 * <ScrollRevealProvider config={{
 *   animationType: 'fadeUp',
 *   duration: 800,
 *   once: false
 * }}>
 *   <App />
 * </ScrollRevealProvider>
 * ```
 */
export const ScrollRevealProvider: React.FC<ScrollRevealProviderProps> = ({
  children,
  config = {},
}) => {
  // Check for reduced motion preference
  const prefersReducedMotion = React.useMemo(() => {
    if (typeof window === 'undefined') return false;
    return window.matchMedia('(prefers-reduced-motion: reduce)').matches;
  }, []);

  // Merge config with reduced motion settings
  const finalConfig = React.useMemo(() => {
    if (config.respectReducedMotion && prefersReducedMotion) {
      return {
        ...config,
        disabled: true,
      };
    }
    return config;
  }, [config, prefersReducedMotion]);

  return (
    <ScrollRevealContext.Provider value={finalConfig}>{children}</ScrollRevealContext.Provider>
  );
};

/**
 * Hook to access global ScrollReveal configuration
 *
 * @example
 * ```tsx
 * const config = useScrollRevealContext();
 * ```
 */
export function useScrollRevealContext(): ScrollRevealConfig | null {
  return useContext(ScrollRevealContext);
}

/**
 * Hook to check if animations should be disabled
 *
 * @example
 * ```tsx
 * const isDisabled = useAnimationDisabled();
 * ```
 */
export function useAnimationDisabled(): boolean {
  const context = useScrollRevealContext();

  if (typeof window === 'undefined') return true;

  if (context?.disabled) return true;

  if (context?.respectReducedMotion) {
    return window.matchMedia('(prefers-reduced-motion: reduce)').matches;
  }

  return false;
}
