import { RefObject, useEffect, useRef, useState, useCallback } from 'react';
import { createObserver, ObserverManager } from '../utils/observer';
import { isSSR } from '../utils/ssr';

export interface UseScrollRevealOptions {
  /** Reference to the target element */
  ref: RefObject<Element>;
  /** Threshold for intersection (0-1) */
  threshold?: number | number[];
  /** Root margin for intersection observer */
  rootMargin?: string;
  /** Whether animation should only play once */
  once?: boolean;
  /** Disable the hook */
  disabled?: boolean;
  /** Custom root element for intersection */
  root?: Element | null;
}

export interface UseScrollRevealReturn {
  /** Whether the element is currently in view */
  isInView: boolean;
  /** Whether the element has been animated (for once mode) */
  hasAnimated: boolean;
  /** Manually trigger reveal */
  reveal: () => void;
  /** Manually hide element */
  hide: () => void;
  /** Reset animation state */
  reset: () => void;
}

/**
 * Custom hook for scroll-based reveal animations
 *
 * @example
 * ```tsx
 * const { isInView, hasAnimated } = useScrollReveal({
 *   ref: elementRef,
 *   threshold: 0.1,
 *   once: true
 * });
 * ```
 */
export function useScrollReveal({
  ref,
  threshold = 0.1,
  rootMargin = '0px',
  once = true,
  disabled = false,
  root = null,
}: UseScrollRevealOptions): UseScrollRevealReturn {
  const [isInView, setIsInView] = useState(false);
  const [hasAnimated, setHasAnimated] = useState(false);
  const observerRef = useRef<ObserverManager | null>(null);
  const isFirstRender = useRef(true);

  // Manual control functions
  const reveal = useCallback(() => {
    setIsInView(true);
    if (once) {
      setHasAnimated(true);
    }
  }, [once]);

  const hide = useCallback(() => {
    if (!once || !hasAnimated) {
      setIsInView(false);
    }
  }, [once, hasAnimated]);

  const reset = useCallback(() => {
    setIsInView(false);
    setHasAnimated(false);
  }, []);

  useEffect(() => {
    // Skip on SSR or if disabled
    if (isSSR() || disabled || !ref.current) {
      return;
    }

    const element = ref.current;

    // Create observer
    observerRef.current = createObserver({
      threshold,
      rootMargin,
      root,
      callback: (entries) => {
        entries.forEach((entry) => {
          if (entry.target === element) {
            const inView = entry.isIntersecting;

            // Update view state
            setIsInView(inView);

            // Handle once mode
            if (inView && once) {
              setHasAnimated(true);
              // Disconnect after first animation
              observerRef.current?.disconnect();
            }
          }
        });
      },
    });

    // Start observing
    observerRef.current.observe(element);

    // Handle initial state for elements already in view
    if (isFirstRender.current) {
      isFirstRender.current = false;

      // Check if element is already in view on mount
      const rect = element.getBoundingClientRect();
      const windowHeight = window.innerHeight || document.documentElement.clientHeight;
      const inViewOnMount = rect.top < windowHeight && rect.bottom > 0;

      if (inViewOnMount) {
        setIsInView(true);
        if (once) {
          setHasAnimated(true);
        }
      }
    }

    // Cleanup
    return () => {
      if (observerRef.current && element) {
        observerRef.current.unobserve(element);
        observerRef.current.disconnect();
      }
    };
  }, [ref, threshold, rootMargin, once, disabled, root]);

  return {
    isInView,
    hasAnimated,
    reveal,
    hide,
    reset,
  };
}

/**
 * Hook for programmatic scroll reveal control
 *
 * @example
 * ```tsx
 * const controls = useScrollRevealControls();
 *
 * // Later in your code
 * controls.revealAll('.reveal-element');
 * controls.hideAll('.reveal-element');
 * ```
 */
export function useScrollRevealControls() {
  const revealAll = useCallback((selector: string) => {
    if (isSSR()) return;

    const elements = document.querySelectorAll(selector);
    elements.forEach((el) => {
      // Trigger custom event that ScrollReveal components can listen to
      el.dispatchEvent(new CustomEvent('scroll-reveal:reveal'));
    });
  }, []);

  const hideAll = useCallback((selector: string) => {
    if (isSSR()) return;

    const elements = document.querySelectorAll(selector);
    elements.forEach((el) => {
      el.dispatchEvent(new CustomEvent('scroll-reveal:hide'));
    });
  }, []);

  const resetAll = useCallback((selector: string) => {
    if (isSSR()) return;

    const elements = document.querySelectorAll(selector);
    elements.forEach((el) => {
      el.dispatchEvent(new CustomEvent('scroll-reveal:reset'));
    });
  }, []);

  return {
    revealAll,
    hideAll,
    resetAll,
  };
}
