/**
 * Check if code is running on server-side
 */
export function isSSR(): boolean {
  return typeof window === 'undefined' || typeof document === 'undefined';
}

/**
 * Safe window access for SSR environments
 */
export function safeWindow<T>(callback: (window: Window) => T, fallback: T): T {
  if (isSSR()) return fallback;
  return callback(window);
}

/**
 * Request animation frame with SSR safety
 */
export function safeRequestAnimationFrame(callback: FrameRequestCallback): number | null {
  if (isSSR()) return null;
  return window.requestAnimationFrame(callback);
}