import { isSSR } from './ssr';

export interface ObserverOptions {
  threshold?: number | number[];
  rootMargin?: string;
  root?: Element | null;
  callback: IntersectionObserverCallback;
}

export interface ObserverManager {
  observer: IntersectionObserver;
  observe: (element: Element) => void;
  unobserve: (element: Element) => void;
  disconnect: () => void;
}

// Global observer cache for performance
const observerCache = new Map<string, ObserverManager>();
const observerCallbacks = new WeakMap<IntersectionObserver, Set<IntersectionObserverCallback>>();

/**
 * Create or reuse an IntersectionObserver instance
 */
export function createObserver(options: ObserverOptions): ObserverManager {
  if (isSSR()) {
    // Return a dummy observer for SSR
    return {
      observer: null as any,
      observe: () => {},
      unobserve: () => {},
      disconnect: () => {},
    };
  }

  const { threshold = 0, rootMargin = '0px', root = null, callback } = options;

  // Create a unique key for caching
  const key = `${threshold}-${rootMargin}-${root?.id || 'null'}`;

  // Check cache first
  if (observerCache.has(key)) {
    const cached = observerCache.get(key)!;
    // Add callback to the set of callbacks for this observer
    const callbacks = observerCallbacks.get(cached.observer) || new Set();
    callbacks.add(callback);
    observerCallbacks.set(cached.observer, callbacks);
    return cached;
  }

  // Create a set to store callbacks
  const callbacks = new Set<IntersectionObserverCallback>();
  callbacks.add(callback);

  // Create new observer with a master callback that calls all registered callbacks
  const observer = new IntersectionObserver(
    (entries, obs) => {
      const callbackSet = observerCallbacks.get(obs);
      if (callbackSet) {
        callbackSet.forEach((cb) => cb(entries, obs));
      }
    },
    {
      threshold,
      rootMargin,
      root,
    }
  );

  // Store callbacks for this observer
  observerCallbacks.set(observer, callbacks);

  const manager: ObserverManager = {
    observer,
    observe: (element: Element) => {
      observer.observe(element);
    },
    unobserve: (element: Element) => {
      observer.unobserve(element);
    },
    disconnect: () => {
      observer.disconnect();
      observerCache.delete(key);
      observerCallbacks.delete(observer);
    },
  };

  // Cache the observer
  observerCache.set(key, manager);

  return manager;
}

/**
 * Batch observer operations for performance
 */
export class BatchObserver {
  private queue: Array<{ action: 'observe' | 'unobserve'; element: Element }> = [];
  private observer: ObserverManager;
  private rafId: number | null = null;

  constructor(options: ObserverOptions) {
    this.observer = createObserver(options);
  }

  observe(element: Element): void {
    this.queue.push({ action: 'observe', element });
    this.scheduleFlush();
  }

  unobserve(element: Element): void {
    this.queue.push({ action: 'unobserve', element });
    this.scheduleFlush();
  }

  disconnect(): void {
    if (this.rafId !== null) {
      cancelAnimationFrame(this.rafId);
    }
    this.observer.disconnect();
  }

  private scheduleFlush(): void {
    if (this.rafId !== null || isSSR()) return;

    this.rafId = requestAnimationFrame(() => {
      this.flush();
      this.rafId = null;
    });
  }

  private flush(): void {
    const actions = [...this.queue];
    this.queue = [];

    actions.forEach(({ action, element }) => {
      if (action === 'observe') {
        this.observer.observe(element);
      } else {
        this.observer.unobserve(element);
      }
    });
  }
}
